@font-face {
    font-family: "Ending Loving";
    src: url("assets/ending-loving.otf") format("opentype");
}

@font-face {
    font-family: "Amiamie Round";
    src: url("assets/amiamie-round.otf") format("opentype");
}

:root {
    --theme-color: #f2b1ae;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Amiamie Round";
}

html {
    background-color: black;
}

html,
body {
    height: 100%;
    overflow: auto;
}

body {
    background-image: url("assets/background-img.jpg");
    background-size: cover;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-position: right 0;
    padding: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 12px;
}

h1 {
    font-family: "Ending Loving";
    font-size: 3rem;
    color: var(--theme-color);
    text-align: center;
}

#content {
    background-color: black;
    border-radius: 8px;
    border: 1px solid var(--theme-color);
    color: var(--theme-color);
    width: min(800px, 90%);
    padding: 12px;
    font-size: 1.2rem;
    text-align: center;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

nav ul {
    list-style-type: none;
    display: flex;
    justify-content: center;
    gap: 12px;
}

li a {
    text-decoration: none;
    color: var(--theme-color);
}

#active-tab {
    text-decoration: underline;
    text-underline-offset: 3px;
}

#two-scoop-cone {
    width: min(450px, 100%);
}

footer {
    background-color: black;
    color: var(--theme-color);
    width: fit-content;
    padding: 8px;
    border: 1px solid var(--theme-color);
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 4px;
}

footer div {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 10px;
    font-size: 1rem;
}

footer a {
    color: var(--theme-color);
    text-decoration: none;
    display: flex;
    align-items: center;
}

#github-icon {
    height: 1.5rem;
}
